g-top: 100px;\n}\n\n.about {\n  position: relative;\n  width: 100%;\n}\n\n.header {\n  overflow: hidden;\n  background-color: #284562;\n  padding: 15px 10px;\n}\n\n.header a {\n  float: left;\n  color: white;\n  text-align: center;\n  padding: 12px;\n  text-decoration: none;\n  font-size: 18px;\n  font-family: inherit;\n  line-height: 25px;\n  border-radius: 4px;\n}\n\n.header a.logo {\n  font-size: 25px;\n  font-family: inherit;\n  font-weight: bold;\n}\n\n.header a:hover {\n  background-color: #ddd;\n  color: black;\n}\n\n.header a.active {\n  background-color: dodgerblue;\n  color: white;\n}\n\n.header ul {\n  padding-left: 5px;\n  padding-right: 5px;\n}\n\n.header-right {\n  float: right;\n}\n\n.footer {\n  overflow: hidden;\n  background-color: #5e5757e0;\n  color: white;\n  height: auto;\n  padding: 15px 10px;\n  height: relative;\n  bottom: 0;\n  width: 100%;\n}\n\n.footer-left {\n  float: left;\n  padding-left: 15px;\n  padding-bottom: 15px;\n}\n\n.footer-right {\n  float: right;\n  padding-top: 125px;\n  font-size: 10px;\n  padding-right: 15px;\n  padding-bottom: 8px;\n}\n\n.responsive {\n  max-width: 100%;\n  height: auto;\n}\n\n.page_wrpr {\n  max-width: 1200px;\n  margin: 0px auto;\n  text-align: justify;\n  font-family: inherit;\n  text-justify: inter-word;\n}\n\n/* Service Page */\n\n.infinite-photo-grid {\n  width: 100%;\n  height: 100%;\n  max-height: 1200px;\n  /* background-image: url('../images/infinite_loop2.jpg'); */\n  background-size: calc(1.15 * min(100vh, 750px)) min(100vh, 750px);\n  background-repeat: repeat-1.5x;\n  animation: scroll 25s linear infinite;\n}\n\n@keyframes scroll {\n  from {\n    background-position: left calc(4.84 * min(100vh, 750px)) top 0px;\n  }\n\n  to {\n    background-position: left 0px top 0px;\n  }\n}\n\n/* Chat CSS */\n\n.open-button {\n  background-color: rgb(0, 35, 59);\n  color: white;\n  padding: 16px 20px;\n  border: none;\n  cursor: pointer;\n  opacity: 0.8;\n  position: fixed;\n  bottom: 23px;\n  right: 28px;\n  width: 280px;\n}\n\n/* The popup chat - hidden by default */\n.close-chat-popup {\n  display: none;\n  position: fixed;\n  bottom: 0;\n  right: 15px;\n  border: 3px solid #f1f1f1;\n  z-index: 9;\n}\n\n.open-chat-popup {\n  display: block;\n  position: fixed;\n  bottom: 0;\n  right: 15px;\n  border: 3px solid #f1f1f1;\n  z-index: 9;\n}\n\n/* Add styles to the form container */\n.form-container {\n  max-width: 300px;\n  padding: 10px;\n  background-color: white;\n}\n\n/* Full-width textarea */\n.form-container textarea {\n  width: 100%;\n  padding: 15px;\n  margin: 5px 0 22px 0;\n  border: none;\n  background: #f1f1f1;\n  resize: none;\n  min-height: 200px;\n}\n\n/* When the textarea gets focus, do something */\n.form-container textarea:focus {\n  background-color: rgb(4, 48, 74);\n  outline: none;\n}\n\n/* Set a style for the submit/send button */\n.form-container .btn {\n  background-color: #04AA6D;\n  color: white;\n  padding: 16px 20px;\n  border: none;\n  cursor: pointer;\n  width: 100%;\n  margin-bottom:10px;\n  opacity: 0.8;\n}\n\n/* Add a red background color to the cancel button */\n.form-container .cancel {\n  background-color: red;\n}\n\n/* Add some hover effects to buttons */\n.form-container .btn:hover, .open-button:hover {\n  opacity: 1;\n}\n\n/* Chat over */\n\n/* contact page */\n\n.formboldmb5 {\n  margin-bottom: 20px;\n}\n\n.formbold-pt-3 {\n  padding-top: 12px;\n}\n\n.formboldmainwrapper {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 48px;\n}\n\n.formboldformwrapper {\n  margin: 0 auto;\n  max-width: 550px;\n  width: 100%;\n  background: FFFBF1;\n}\n\n.formboldformlabel {\n  display: block;\n  font-weight: 500;\n  font-size: 16px;\n  color: #07074d;\n  margin-bottom: 12px;\n}\n\n.formboldformlabel2 {\n  font-weight: 600;\n  font-size: 20px;\n  margin-bottom: 20px;\n}\n\n.formboldforminput {\n  width: 100%;\n  padding: 12px 24px;\n  border-radius: 6px;\n  border: 1px solid #e0e0e0;\n  background: FFFBF1;\n  font-weight: 500;\n  font-size: 16px;\n  color: #6b7280;\n  outline: none;\n  resize: none;\n}\n\n.formbold-form-input:focus {\n  border-color: #14487e;\n  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.05);\n}\n\n.formboldbtn {\n  text-align: center;\n  font-size: 16px;\n  border-radius: 6px;\n  padding: 14px 32px;\n  border: none;\n  color: white;\n  font-weight: 600;\n  background-color: #284562;\n  color: FFFBF1;\n  cursor: pointer;\n}\n\n.formboldbtn:hover {\n  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.05);\n}\n\n.formbold--mx-3 {\n  margin-left: -12px;\n  margin-right: -12px;\n}\n\n.formbold-px-3 {\n  padding-left: 12px;\n  padding-right: 12px;\n}\n\n.flex {\n  display: flex;\n}\n\n.flex-wrap {\n  flex-wrap: wrap;\n}\n\n.w-full {\n  width: 100%;\n}\n\n@media screen and (max-width: 600px) {\n  .header a {\n    float: none;\n    display: block;\n    text-align: left;\n  }\n\n  .header-right {\n    float: none;\n  }\n\n  .sm\\:w-half {\n    width: 50%;\n  }\n\n  .home-left, .home-right{\n    width: 100%;\n    padding: 20px;\n  }\n}\n\n\ndiv.a {\n  position: relative;\n  width: 100%;\n  height:auto;\n}\n\ndiv.text2 {\n  width: 100%;\n  padding: 60px;\n  font-size: 15px;\n  opacity: .95;\n  text-align: justify;\n}\n\n@media screen and (max-width: 600px) {\n\n  div.a,\n  div.text2 {\n    width: 100%;\n    padding: 0;\n  }\n}\n\ndiv.a1 {\n  position: relative;\n  width: 100%;\n  min-height: 300px;\n  padding: 50px;\n}\n\ndiv.a2 {\n  position: relative;\n  width: 100%;\n  min-height: 300px;\n  padding: 50px;\n}\n\ndiv.icon1 {\n  padding: 30px;\n  width: 40%;\n  float: left\n}\n\n.icon {\n  width: 100%;\n  max-width: 220px;\n  max-height: 220px;\n  align-items: center;\n}\n\ndiv.t1 {\n  padding-right: 25px;\n  width: 60%;\n  float: left;\n  text-align: justify;\n}\n\ndiv.icon2 {\n  padding-top: 30px;\n  padding-left: 70px;\n  float: right;\n  width: 40%;\n  align-content: center;\n}\n\ndiv.t2 {\n  float: left;\n  width: 60%;\n  text-align: justify;\n}\n\n/* .infinite-photo-grid {\n  width: 100%;\n  height: 100%;\n  max-height: 1200px;\n  background-image: url(images/infinite_loop2.jpg);\n  background-size: calc(1.15 * min(100vh, 750px)) min(100vh, 750px);\n  background-repeat: repeat-1.5x;\n  animation: scroll 25s linear infinite;\n} */\n\n@media screen and (max-width: 600px) {\n\n  div.icon1,\n  div.t1 {\n      width: 100%;\n      padding: 10px;\n      align-content: center;\n  }\n\n  div.icon2,\n  div.t2 {\n      width: 100%;\n      padding: 10px;\n      align-content: center;\n  }\n}\n"],"sourceRoot":""}]);
