t: \"&#10006;\";\n}\n\n.row {\n    min-height: 100vh;\n}\n\n.row:after {\n    content: \"\";\n    display: table;\n    clear: both;\n}\n\n.register {\n    background-color: #FFFFFF;\n    width: 400px;\n    height: 650px;\n    margin: 5em auto;\n    border-radius: 1.5em;\n    box-shadow: 0px 11px 35px 2px rgba(0, 0, 0, 0.14);\n}\n\n.login {\n    background-color: #FFFFFF;\n    border-radius: 1.5em;\n    width: 50%;\n    margin: 5em auto;\n    box-shadow: 0px 11px 35px 2px rgba(0, 0, 0, 0.14);\n}\n\n.title {\n    padding-top: 40px;\n    color: #284562;\n    font-family: Optima, sans-serif;\n    font-weight: bold;\n    font-size: 23px;\n}\n\ninput,\nselect,\ntextarea {\n    width: 75%;\n    padding: 10px 20px;\n    font-size: 14px;\n    letter-spacing: 1px;\n    border: 1px solid #ccc;\n    border-radius: 4px;\n    box-sizing: border-box;\n    margin-bottom: 50px;\n    margin-left: 46px;\n    text-align: center;\n    margin-bottom: 27px;\n    resize: vertical;\n    background: azure;\n}\n\ninput:focus {\n    border: 2px solid rgba(0, 0, 0, 0.18) !important;\n\n}\n\n.submit,\n.delete {\n    text-align: center;\n    font-size: 16px;\n    border-radius: 6px;\n    padding: 14px 32px;\n    border: none;\n    font-weight: 600;\n    background-color: #284562;\n    color: white;\n    cursor: pointer;\n}\n\n.submit.forpass {\n    /* margin-right: 2em; */\n    color: white;\n}\n\n.submit.reg {\n    margin-left: 2em;\n    color: white;\n}\n\n.submit:disabled {\n    cursor: not-allowed;\n    pointer-events: all !important;\n}\n\n.regsubmit {\n    width: 40%;\n    margin-left: 7em;\n    text-align: center;\n    font-size: 16px;\n    border-radius: 6px;\n    padding: 14px 32px;\n    border: none;\n    font-weight: 600;\n    background-color: #284562;\n    color: white;\n    cursor: pointer;\n}\n\n.forgot>a {\n    text-shadow: 0px 0px 3px rgba(117, 117, 117, 0.12);\n    color: #284562;\n    text-decoration: none;\n    padding-top: 15px;\n}\n\na {\n    text-decoration: none;\n}\n\n@me